<template>
    <article :id="item.id" class="flex  gap-6 w-[49%] bg-white rounded-xl px-6 py-4">
        <i class="fa fa-user-circle text-gray-300 text-[7rem]"></i>
        <div class="text-left">
            <h3 class="text-xl font-bold">{{ item.name }}</h3>         
            <span>{{ item.email }}</span>
            <div class="flex gap-6 py-4">
                <span class="flex items-center gap-1"><i class="fa" :class="isActive(item.active)"></i> Activo</span>
                <span class="flex items-center gap-1"><i class="fa text-white font-bold p-1 rounded-full" :class="isActive(item.verify)"></i> Verificado</span>
            </div>   
        </div>
        <div class="flex gap-2 self-center ml-auto">
            <i class="fa fa-edit text-3xl text-blue cursor-pointer" @click="updateVendor(item.id)"></i>
            <i class="fa fa-times-circle text-3xl text-red cursor-pointer"></i>
        </div>
    </article>
</template>
<script>

export default {
    name: 'TableComp',
    props: {
        item: {
            default: {}
        }
    },    
    data() {
        return {
        }
    },
    methods: {
        isActive(value) {
            return (value > 0) ? 'text-white font-bold p-1 rounded-full bg-green-400 fa-check' : 'text-white text-2xl text-red fa-times-circle'
        },
        updateVendor(vendorId) {
            this.$emit('updVendor', vendorId)
        }
    }
}
</script>
